
.food {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  background: #fff;
}
.food.move-enter-active,
.food.move-leave {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food.move-enter,
.food.move-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.food .image-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.food .image-header img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food .image-header .back {
  position: absolute;
  top: 10px;
  left: 0;
}
.food .image-header .back .icon-arrow_left {
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.food .content {
  position: relative;
  padding: 18px;
}
.food .content .title {
  margin-bottom: 8px;
  line-height: 14px;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
}
.food .content .detail {
  margin-bottom: 18px;
  font-size: 0;
  height: 10px;
  line-height: 10px;
}
.food .content .detail .sell-count,
.food .content .detail .rating {
  font-size: 10px;
  color: #93999f;
}
.food .content .detail .sell-count {
  margin-right: 12px;
}
.food .content .price {
  line-height: 24px;
  font-weight: 700;
}
.food .content .price .now {
  font-size: 14px;
  font-weight: 700;
  color: #f01414;
  margin-right: 8px;
}
.food .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #93999f;
}
.food .content .cartcontrol-wrapper {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.food .content .buy {
  position: absolute;
  bottom: 18px;
  right: 18px;
  line-height: 24px;
  height: 24px;
  padding: 0 12px;
  border-radius: 12px;
  background: #00a0dc;
  color: #fff;
  font-size: 10px;
  text-align: center;
  opacity: 1;
}
.food .content .buy.fade-enter-active,
.food .content .buy.fade-leave-active {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.food .content .buy.fade-enter-active,
.food .content .buy.fade-leave {
  opacity: 0;
  z-index: -1;
}
.food .food-info {
  padding: 18px;
  background: #fff;
}
.food .food-info .info-title {
  font-size: 14px;
  font-weight: 500;
}
.food .food-info .info {
  line-height: 24px;
  padding: 6px 8px 0 8px;
  font-size: 12px;
  color: #4d555d;
}
.food .food-content>.rating {
  padding-top: 18px;
  background: #fff;
}
.food .food-content>.rating .title {
  padding-left: 18px;
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 6px;
}
.food .food-content>.rating .rating-wrapper {
  border-top: 1px solid rgba(7,17,27,0.1);
  padding: 0 18px;
}
.food .food-content>.rating .rating-wrapper .rating-item {
  position: relative;
  padding: 16px 0;
  font-size: 10px;
  color: #93999f;
  line-height: 12px;
  position: relative;
}
.food .food-content>.rating .rating-wrapper .rating-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.food .food-content>.rating .rating-wrapper .rating-item:last-of-type:after {
  border: none;
}
.food .food-content>.rating .rating-wrapper .rating-item .user {
  position: absolute;
  right: 0;
  top: 16px;
}
.food .food-content>.rating .rating-wrapper .rating-item .user .avatar {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}
.food .food-content>.rating .rating-wrapper .rating-item .user .name {
  vertical-align: top;
}
.food .food-content>.rating .rating-wrapper .rating-item .text {
  margin-top: 6px;
  color: #07111b;
  line-height: 16px;
}
.food .food-content>.rating .rating-wrapper .rating-item .text span {
  margin-right: 4px;
  line-height: 24px;
}
.food .food-content>.rating .rating-wrapper .rating-item .text span.icon-thumb_up {
  color: #00a0dc;
}
.food .food-content>.rating .rating-wrapper .rating-item .text span.icon-thumb_down {
  color: #93999f;
}
.food .food-content>.rating .no-rating {
  padding: 16px 0;
  font-size: 12px;
  color: #93999f;
}


