
.ratingselect {
  padding: 0 18px;
}
.ratingselect>.border-1px {
  margin-top: 12px;
  padding-bottom: 18px;
  position: relative;
}
.ratingselect>.border-1px:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.ratingselect>.border-1px div {
  display: inline-block;
  margin-right: 8px;
  padding: 8px 12px;
  font-size: 12px;
  line-height: 16px;
  border-radius: 2px;
}
.ratingselect>.border-1px div span {
  margin-left: 5px;
  font-size: 8px;
}
.ratingselect>.border-1px div.positive {
  background: rgba(0,160,220,0.2);
  color: #4d555d;
}
.ratingselect>.border-1px div.positive.active {
  background: #00a0dc;
  color: #fff;
}
.ratingselect>.border-1px div.negative {
  background: rgba(77,85,93,0.2);
  color: #4d555d;
}
.ratingselect>.border-1px div.negative.active {
  background: #00a0dc;
  color: #fff;
}
.ratingselect .switch {
  line-height: 24px;
  padding: 12px 0;
  font-size: 12px;
  color: #93999f;
}
.ratingselect .switch.active .icon-check_circle,
.ratingselect .switch.active .text {
  color: #00c850;
}
.ratingselect .switch .icon-check_circle {
  font-size: 24px;
  margin-right: 4px;
}
.ratingselect .switch .text {
  vertical-align: top;
}


